import {
    order
} from 'config/request.js';

module.exports = {
    name: 'order-list',
    data() {
        return {
            article_list: [],

            format_status_list: {
                11: '',
                20: '',
                30: '',
                40: '',
                0: '',
            },
            options: [{
                value: '11',
                label: ''
                }, {
                value: '20',
                label: ''
                }, {
                value: '30',
                label: ''
                }, {
                value: '40',
                label: ''
                }, {
                value: '0',
                label: ''
            }],
            value5: [],
              //
            search_data: {
                username: '',
                chname: '',
                status: [],
            },
            batch_id: '', //id
            batch_flag: true, //true,false

            //
            paginations: {
                current_page: 1,
                total: 0,
                page_size: 12,
                page_sizes: [3, 9, 12, 24],
                layout: "total, sizes, prev, pager, next, jumper"
            },

            fields: {
                status: {
                    info: {
                        prop: 'status',
                        label: '',
                        sortable: true
                    },

                    filter: {
                        list: [{
                            text: '',
                            value: 11
                        }, {
                            text: '',
                            value: 20
                        }, 
                        {
                            text: '',
                            value: 30
                        },{
                            text: '',
                            value: 40
                        }, {
                            text: '',
                            value: 0
                        }],
                        multiple: true
                    },
                    style: {
                        width: '180',
                        align: 'center'
                    }
                },
                create_time: {
                    info: {
                        prop: 'add_time',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '260',
                        align: 'center'
                    }
                },
                name: {
                    info: {
                        prop: 'user_name',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '150',
                        align: 'center'
                    }
                },
                 order_sn: {
                    info: {
                        prop: 'order_sn',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '150',
                        align: 'center'
                    }
                },
                channel_info: {
                    info: {
                        prop: 'channel_info.user_name',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '150',
                        align: 'center'
                    }
                },
                price: {
                    info: {
                        prop: 'final_amount',
                        label: '',
                        sortable: true
                    },
                    filter: {

                    },
                    style: {
                        width: '150',
                        align: 'center'
                    }
                }
            }
        }
    },
    methods: {

        /**
         * 
         */
        formatterStatus(item) {
            return this.format_status_list[item.status];
        },
        filterStatus(value, item) {
            return this.format_status_list[value] == this.format_status_list[item.status];
        },
          /**
         * 
         */
        onSearch() {
            var sd = {};
            var query = this.$route.query;
            for (var p in query) {
                sd[p] = query[p];
            }
            for (var s in this.search_data) {
                if(s == 'status'){
                    sd[s+'[]'] = this.search_data[s];
                }
                else{
                    sd[s] = this.search_data[s];
                }   
                if (!sd[s]) {
                    delete sd[s];
                }  
            }
         this.getList({});
        },

        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.paginations.current_page = page;
            this.getList({
                page,
                
            });
        },

        /**
         * 
         * @param  {number} size 
         */
        onChangePageSize(page_size) {
            this.paginations.page_size = page_size;

            this.getList({
                page_size,
               
            });
        },

        /**
         * 
         * @param  {number} options.page      
         * @param  {number} options.page_size 
         * @param  {function} options.fn                            } 
         */
        getList() {
            var data = {
                page: this.paginations.current_page,
                page_size: this.paginations.page_size
            };
            var query = this.$route.query;
            if (query.chname) {
                this.search_data.chname = query.chname;
            };    

            for (var k in this.search_data) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = this.search_data[k];
                    data[k] = this.search_data[k];
                }
            }

    

            this.$$api_order_selectOrder(data, (order_data) => {
                this.article_list = order_data.data;
                this.paginations.total = order_data.meta.pagination.total
                
            });


        },
    },
    mounted() {
        this.getList();
    },
    '$route' (to, from) {

    }
}


