<template>
    <div class="form">
        <el-form style="margin:20px;width:60%;min-width:600px;" 
            label-width="100px" 
            :model="user_data"
            ref='user_data'>


           <el-form-item class='edit-form' 
                label="" 
                prop='user_name'>
                <el-input
                 :disabled=true
                 style="width:50%;"
                        v-model="user_data.users.user_name" placeholder='0.00'></el-input>
            </el-form-item>

            <el-form-item class='edit-form' 
                label="" 
                prop='cash_money'>
                <el-input
                 :disabled=true
                 style="width:50%;"
                        v-model="user_data.cash_money" placeholder='0.00'></el-input>
            </el-form-item>


            <el-form-item class='edit-form' 
                label="" 
                prop='bank_card'>
                <el-input
                 :disabled=true
                        v-model="user_data.bank_card" placeholder=''></el-input>
            </el-form-item>
           <el-form-item class='edit-form' 
                label="" 
                prop='bank_card'>
                <el-input
                 :disabled=true
                        v-model="user_data.bank_name" placeholder=''></el-input>
            </el-form-item>

            <el-form-item class="edit-form" 
                label=''
                 :disabled=true
                prop='bank_address'>
                <el-input type='textarea' 
                   :disabled=true
                    v-model="user_data.bank_address"
                    placeholder=''></el-input>
            </el-form-item>

            <el-form-item class='edit-form' 
                label="" 
                prop='card_name'>
                <el-input
                        :disabled='true'
                        v-model="user_data.card_name" placeholder=''></el-input>
               </el-form-item>
            </el-form-item>
      
           <el-form-item class='edit-form' 
                  label="" 
                  prop='card_name'>
                 <el-radio-group 
               
                 v-model="radio2" 
                 @change="changeStatus"
                 >
                <el-radio :label="0"   :disabled="cash_status" ></el-radio>
                <el-radio :label="1"   :disabled="cash_status" ></el-radio>
                <el-radio :label="2"   :disabled="cash_status" ></el-radio>
                <el-radio :label="3"   :disabled="money_status" ></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item class='edit-form' 
                label="" 
                prop='card_name'>
                <el-input
                        :disabled="msg_status"
                        type="textarea"
                        v-model="user_data.msg" placeholder=''></el-input>
               </el-form-item>
            </el-form-item>



            <el-form-item>
                <el-button  :disabled="sub_status"  type="primary" @click='save_user("user_data")'></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import CashJs from './Cash.js';
    module.exports=CashJs;
</script>

<style scoped>
 .edit-form{
        width:500px;
    }



  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>



