module.exports = {
    name: 'list',
    data() {
        return {
            user_list: [], //
            currentPage: 1,
            page_size:10,
            page_sizes:[10, 100, 200, 300],
            loading2:0,
            //
            search_data: {
                user_name: '',
            },
            //
            paginations: {
                current_page: 1,
                total: 0,
                page_size: 12,
                page_sizes: [3, 9, 12, 24],
                layout: "total, sizes, prev, pager, next, jumper"
            },
                  //
            sex_filters: {
                list: [{
                    text: '',
                    value: 0
                }, {
                    text: '',
                    value: 1
                },{
                    text: '',
                    value: 2
                }
                ,{
                    text: '',
                    value: 2
                }
                ],
                multiple: true
            },
     
        }
    },
    methods: {

   /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterSex(item,val) {
          if (item.status == 0) 
          {
              return ''
          }
          else if (item.status == 1) 
          {
            return ''
          }
           else if (item.status == 2) 
          {
             return '('+item.msg+')'
          }
          else
          { 
            return ''
          }
        },
         /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterSex(value, item) {
            return item.status == value;
        },

           /**
         * 
         * @param  {object} user 
         */
        onCash(user) {
            if (user && user.id) {
                this.$router.push('/tongji/cash/shenhe?id=' + user.id);
            } else {
                this.$message({
                    showClose: true,
                    message: 'ID',
                    type: 'error'
                });
            }
        },





        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.paginations.current_page = page;
            this.getList({
                page,
                
            });
        },

        /**
         * 
         * @param  {number} size 
         */
        onChangePageSize(page_size) {
            this.paginations.page_size = page_size;
            this.getList({
                page_size,
               
            });
        },

        /**
         * 
         */
        onSearch() {
            this.getList();
        },

        /**
         * 
         */
        getList() {
            var data = {
                page: this.paginations.current_page,
                page_size: this.paginations.page_size
            };
            var query = this.$route.query;
    
            for (var k in query) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = query[k];
                    data[k] = query[k];
                }
            }

            for (var k in this.search_data) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = this.search_data[k];
                    data[k] = this.search_data[k];
                }
            }

            this.$$api_user_selectCash(data, (data) => {
                this.user_list = data.data.data;
                this.paginations.total = data.data.total;
            });


        }
    },

    mounted() {
        this.getList();
    },
    watch: {
        '$route' (to, from) {
            this.getList();
        }
    }
}


