<template>
    <div class="form">
        <el-form style="margin:20px;width:60%;min-width:600px;" 
            label-width="100px" 
            :model="user_data"
            :rules="user_rules"
            ref='user_data'>
            <el-form-item class='edit-form' 
                label="" 
                prop='email'>
                <el-input
                        :disabled='user_data.id ? true : false'
                        v-model="user_data.email" placeholder=''></el-input>
            </el-form-item>

            <el-form-item class='edit-form' 
                label="" 
                prop='user_name'>
                <el-input 
                    :disabled='user_data.id ? true : false'
                    v-model="user_data.user_name" 
                    placeholder=''></el-input>
            </el-form-item>

            <div v-if="user_data.member_type == 0 && user_data.openid == '' " >
            <el-form-item label="">
                <el-switch 
                on-color="#13ce66"
                off-color="#ff4949"
                    on-text="" 
                    off-text=""
                    v-model="is_qudao"
                    @change='changeQudao($event)'
                    ></el-switch>
            </el-form-item>
          </div>    


<div v-if="user_data.member_type == 2">

            <el-form-item class='edit-form' 
                label="" 
                prop='name'>
                <el-input
                        :disabled='user_data.id ? true : false'
                        v-model="user_data.name" placeholder=''></el-input>
            </el-form-item>
        
        <el-form-item style="width:100%;" 
        prop='selectedOptions2'    
        label="" >
  <el-cascader
    expand-trigger="hover"
    placeholder=""
    :options="options"
    v-model="user_data.region"
    @change="handleChange">
  </el-cascader>
  </el-form-item>

       
            <el-form-item class="edit-form" 
                label=''
                prop='address'>
                <el-input type='textarea' 
                    v-model="user_data.address"
                    placeholder=''></el-input>
            </el-form-item>


            <el-form-item class='edit-form' 
                label="" 
                prop='jigou'>
                <el-input
                        :disabled='user_data.id ? true : false'
                        v-model="user_data.jigou" placeholder=''></el-input>
            </el-form-item>



            <el-form-item class='' 
                label="" 
                prop='image_url'>

<el-row>
  <el-col :span="8">
    <el-card :body-style="{ padding: '0px' }">
     <img :src="user_data.image_url" class="image">
    </el-card>
  </el-col>
</el-row>

 </el-form-item>


</div>

<div v-if="user_data.member_type != 0" >
            <el-form-item label="">
                <el-switch 
                on-color="#13ce66"
                off-color="#ff4949"
                    on-text="" 
                    off-text=""
                    v-model="cstatus"
                    @change='changeStatus($event)'
                    ></el-switch>
            </el-form-item>
 </div>           

             <el-form-item
             v-if="user_data.openid == '' " 
             label="()"
                prop='password'>
                <el-input type="password" auto-complete="off" placeholder="" 
                    v-model='user_data.password'
                  ></el-input>
             </el-form-item>


            <el-form-item>
                <el-button type="primary" @click='save_user("user_data")'>{{user_data.id ? '' : ''}}</el-button>
                <el-button type="default"
                           v-if="!user_data.id"
                           @click='reset_user("user_data")'></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import EditJs from './Edit.js';
    module.exports=EditJs;
</script>

<style scoped>
 .edit-form{
        width:500px;
    }



  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>



