module.exports = {
    name: 'list',
    data() {
        return {
            user_list: [], //
            currentPage: 1,
            page_size:10,
            page_sizes:[10, 100, 200, 300],
            loading2:0,
                 //
            paginations: {
                current_page: 1,
                total: 0,
                page_size: 12,
                page_sizes: [3, 9, 12, 24],
                layout: "total, sizes, prev, pager, next, jumper"
            },
            batch_id: '', //id
            batch_flag: true, //true,false

            //
            search_data: {
                user_name: '',
                chname: '',
            },

            //
            dialog: {
                show: false,
                user_info: {}
            },

            //
            sex_filters: {
                list: [{
                    text: '',
                    value: 0
                }, {
                    text: '',
                    value: 1
                },{
                    text: '',
                    value: 2
                }
                ],
                multiple: true
            },

            //
            status_filters: {
                list: [{
                    text: '',
                    value: 1
                }, {
                    text: '',
                    value: 2
                }],
                multiple: true
            },
             options: [
             {
              value: '0',
              label: ''
            }, 
             {
              value: '1',
              label: ''
            }, 
            {
              value: '2',
              label: ''
            }],
            switcht:1,
            switchf:0,
         
            
     
        }
    },
  
    methods: {

        /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterSex(item) {
          if (item.member_type == 0) 
          {
              return ''
          }
          else if (item.member_type == 1) 
          {
            return '()'
          }
          else
          { 
            return '()'
          }
        },


        /**
         * 
         * @param  {object} item 
         * @return {string}      
         */
        formatterStatus(item) {
            return item.status == 1 ? '' : '';
        },


        /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterSex(value, item) {
            return item.member_type == value;
        },


        /**
         * 
         * @param  {number} value 
         * @param  {object} item  
         * @return {boolean}       true,false
         */
        filterStatus(value, item) {
            return item.status == value;
        },


        /**
         * 
         */
        onSearch() {
            // console.log(this.search_data);

            var sd = {};

            var query = this.$route.query;
            for (var p in query) {
                sd[p] = query[p];
            }

            for (var s in this.search_data) {
                sd[s] = this.search_data[s];

                if (!sd[s]) {
                    delete sd[s];
                }
            }


            this.$router.push({
                path: this.$route.path,
                query: sd
            });
        },


        /**
         * CheckBox
         * @param  {array} val 
         */
        onSelectionChange(val) {
            // console.log(val);
            if (val.length) {
                this.batch_flag = false;
                var ids = [];
                for (var i = 0; i < val.length; i++) {
                    ids.push(val[i].id);
                }
                this.batch_id = ids.join(',');
            } else {
                this.batch_flag = true;
                this.batch_id = '';
            }
        },


        /**
         * 
         * @param  {number} page 
         */
        onChangeCurrentPage(page) {
            this.paginations.current_page = page;
            this.getList({
                page,
                
            });
        },


        /**
         * 
         * @param  {number} size 
         */
        onChangePageSize(size) {
            this.paginations.page_size = size;
            this.getList({
                size,
            });
        },


        onAccessUser(user, index, list) {
            this.$$accessUser({
                id: user.id
            }, (data) => {
                user.status = user.status == 1 ? 2 : 1;
            });
        },




        /**
         * 
         * @param  {object} user 
         */
        onEditUser(user) {
            if (user && user.user_id) {
                this.$router.push('/tongji/user/edit?id=' + user.user_id);
            } else {
                this.$message({
                    showClose: true,
                    message: 'ID',
                    type: 'error'
                });
            }
        },


        /**
         * 
         * @param  {object} user 
         */
        onSelectUser(user) {
            if (user && user.uid) {
                this.$router.push('/demo/order/list?chname=' + user.name);
            } else {
                this.$message({
                    showClose: true,
                    message: 'ID',
                    type: 'error'
                });
            }
        },


        /**
         * 
         */
        getList() {

            var data = {
                page: this.paginations.current_page,
                page_size: this.paginations.page_size
            };
            var query = this.$route.query;
            if (query.chname) {
                this.search_data.chname = query.chname;
            };    

            for (var k in this.search_data) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = this.search_data[k];
                    data[k] = this.search_data[k];
                }
            }

            for (var k in query) {
                if (this.search_data[k] !== undefined) {
                    this.search_data[k] = query[k];
                    data[k] = query[k];
                }
            }
            this.$$api_user_selectUserList(data, (data) => {
                this.user_list = data.data.data;
                this.paginations.total = data.data.total;
            });


        }
    },

    mounted() {
        this.getList();

        //test dialog

        /* setTimeout(() => {
             this.onSelectUser(this.user_list[0]);
         }, 600);*/
    },
    watch: {
        '$route' (to, from) {
            this.getList();
        }
    }
}


